@import "https://fonts.googleapis.com/css2?family=Muli:ital,wght@0,700;1,400&display=swap";
* {
  box-sizing: border-box;
}

body {
  height: 100vh;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0;
  font-family: Muli, sans-serif;
  display: flex;
  overflow: hidden;
}

.container {
  width: 90vw;
  gap: 10px;
  display: flex;
}

.panel {
  height: 80vh;
  color: #fff;
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-size: auto 100%;
  border-radius: 50px;
  flex: .5;
  margin: 10px;
  transition: flex .7s ease-in;
  position: relative;
}

.panel h3 {
  opacity: 0;
  margin: 0;
  font-size: 24px;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.panel.active {
  flex: 5;
}

.panel.active h3 {
  opacity: 1;
}

